import echarts from "echarts";
const innerWidth = window.innerWidth;

const commonYconfig = {
  type: "value",
  axisLabel: {
    formatter: "{value}",
    textStyle: {
      color: "#ffffff",
      fontSize: (innerWidth * 0.73) / 100
    }
  },
  nameTextStyle: {
    color: "#ffffff",
    // align: 'right',
    fontSize: (innerWidth * 0.73) / 100
    // lineHeight: 10,
  },
  axisLine: {
    lineStyle: {
      color: "rgba(73, 81, 122, 0.5)"
    }
  },
  axisTick: {
    show: false
  },
  splitLine: {
    show: true,
    lineStyle: {
      color: "rgba(73, 81, 122, 0.5)",
      type: "dashed"
    }
  },
  minorTick: {
    splitNumber: 5
  }
};

const commonXconfig = {
  type: "category",
  boundaryGap: true,
  axisLabel: {
    color: "#ffffff"
  },
  axisLine: {
    show: true,
    lineStyle: {
      color: "rgba(73, 81, 122, 0.5)"
    }
  },
  axisTick: {
    show: false
  },
  splitLine: {
    show: false
  }
};
const gridConfig = {
  left: "4%",
  right: "4%",
  top: "20%",
  bottom: "2%",
  containLabel: true
};
let xs = [];
let data1 = [];
let data2 = [];
let data3 = [];
for (let i = 0; i < 31; i++) {
  if (i + 1 < 10) {
    xs.push("03-0" + (i + 1));
  } else {
    xs.push("03-" + (i + 1));
  }
  let j = parseInt(Math.round(Math.random() * 800 + 700));
  let k = parseInt(Math.round(Math.random() * 800 + 700));
  let q = parseInt(Math.round(Math.random() * 800 + 700));
  data1.push(j);
  data2.push(q);
  data3.push(k);
}

let xs2 = ["合肥", "芜湖", "南京", "淮南", "阜阳", "其他"];
let data4 = [];
let data5 = [];
let data6 = [];
for (let i = 0; i < xs2.length; i++) {
  let j = parseInt(Math.round(Math.random() * 800 + 700));
  let k = parseInt(Math.round(Math.random() * 80 + 20));
  let q = parseInt(Math.round(Math.random() * 80 + 20));
  data4.push(j);
  data5.push(q);
  data6.push(k);
}

const commonDataZoomConfig = [
  {
    //滑动条
    zoomOnMouseWheel: false,
    moveOnMouseMove: true,
    moveOnMouseWheel: true,
    orient: "horizontal",

    // zoomLock: true,
    // maxValueSpan:9,
    xAxisIndex: 0, //这里是从X轴的0刻度开始
    show: false, //是否显示滑动条，不影响使用
    type: "inside", // 这个 dataZoom 组件是 slider 型 dataZoom 组件
    startValue: 0, // 从头开始。
    endValue: 7 // 一次性展示10个。
  }
];

