<template>
  <div class="statistics-lane-vehicle">
    <box>
      <div class="echarts-content">
        <div class="search-echarts">
          <a-form layout="inline" :form="form" @submit="handleSubmit">
            <a-form-item label="统计类别：">
              <a-select
                class="default-select"
                placeholder="请选择"
                dropdown-class-name="default-select-dropdown"
                :get-popup-container="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="1">
                  蓝色
                </a-select-option>
              </a-select>
            </a-form-item>
            <a-form-item label="时间区间：">
              <a-date-picker
                placeholder="请选择时间"
                format="YYYY-MM-DD hh:mm:ss"
                class="default-date-picker"
                show-time
                dropdown-class-name="default-date-picker-dropdown"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-item>
            <a-form-item label="通行时间：">
              <a-range-picker
                separator=""
                show-time
                dropdown-class-name="default-range-picker-dropdown"
                class="default-range-picker"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-item>
            <a-form-item>
              <a-button
                class="operation-button"
                type="primary"
                html-type="submit"
              >
                <a-icon type="search" /> 搜索
              </a-button>
            </a-form-item>
          </a-form>
        </div>
        <div class="statistic-total-num">
          <div class="statistic-total-num-item">
            <div>
              <div class="total-num-item-title">
                <div class="title-icon"></div>
                通行数量
              </div>
              <div class="total-num-item-number">
                2345,658
              </div>
            </div>
            <img
              class="total-num-item-img"
              src="../../assets/img/charts/img-total-number-0.png"
            />
          </div>
          <div class="statistic-total-num-item">
            <div>
              <div class="total-num-item-title">
                <div class="title-icon title-icon1"></div>
                预警数量
              </div>
              <div class="total-num-item-number">
                2345,658
              </div>
            </div>

            <img
              class="total-num-item-img"
              src="../../assets/img/charts/img-total-number-1.png"
            />
          </div>
          <div class="statistic-total-num-item">
            <div>
              <div class="total-num-item-title">
                <div class="title-icon title-icon2"></div>
                处置数量
              </div>
              <div class="total-num-item-number">
                2345,658
              </div>
            </div>
            <img
              class="total-num-item-img"
              src="../../assets/img/charts/img-total-number-2.png"
            />
          </div>
        </div>
        <div class="statistic-echarts">
          <div class="statistic-echarts-top">
            <div class="echarts-top-left">
              <box :is-big-box="false">
                <div class="charts total">
                  <div class="charts-title total-title">
                    <p>Title标题文本</p>
                  </div>
                  <div ref="total-charts" class="charts-div total-charts"></div>
                </div>
              </box>
            </div>
            <div class="echarts-top-right">
              <box :is-big-box="false">
                <div class="charts city">
                  <div class="charts-title city-title">
                    <p>Title标题文本</p>
                  </div>
                  <div ref="city-charts" class="charts-div city-charts"></div>
                </div>
              </box>
            </div>
          </div>
          <div class="statistic-echarts-bottom">
            <div class="echarts-bottom-left">
              <box :is-big-box="false">
                <div class="charts road">
                  <div class="charts-title road-title">
                    <p>Title标题文本</p>
                  </div>
                  <div ref="road-charts" class="charts-div road-charts"></div>
                </div>
              </box>
            </div>
            <div class="echarts-bottom-right">
              <box :is-big-box="false">
                <div class="charts vehicle">
                  <div class="charts-title vehicle-title">
                    <p>Title标题文本</p>
                  </div>
                  <div
                    ref="vehicle-charts"
                    class="charts-div vehicle-charts"
                  ></div>
                  <div class="charts-legend">
                    <div class="legend-item">
                      <div class="item-square item-square1"></div>
                      <div class="item-name">
                        大型车辆
                      </div>
                      <div class="item-percentage">
                        80%
                      </div>
                      <div class="item-number">
                        100辆
                      </div>
                    </div>
                    <div class="legend-item" style="">
                      <div class="item-square item-square2"></div>
                      <div class="item-name">
                        小型车辆
                      </div>
                      <div class="item-percentage">
                        12%
                      </div>
                      <div class="item-number">
                        15辆
                      </div>
                    </div>
                    <div class="legend-item">
                      <div class="item-square item-square3"></div>
                      <div class="item-name">
                        其他车辆
                      </div>
                      <div class="item-percentage">
                        8%
                      </div>
                      <div class="item-number">
                        10辆
                      </div>
                    </div>
                  </div>
                </div>
              </box>
            </div>
          </div>
        </div>
      </div>
    </box>
  </div>
</template>

<script>
import Box from "../../components/Box";
import {
  totalOptions,
  cityOptions,
  roadOptions,
  vehicleOptions
} from "./charts-config";

export default {
  components: {
    Box
  },
  data() {
    return {
      totalInstance: null,
      cityInstance: null,
      roadInstance: null,
      vehicleInstance: null,
      form: this.$form.createForm(this, { name: "horizontal_login" })
    };
  },
  mounted() {
    this.totalInstance = this.$echarts.initCharts(
      this.totalInstance,
      this.$refs["total-charts"],
      totalOptions
    );

    this.cityInstance = this.$echarts.initCharts(
      this.cityInstance,
      this.$refs["city-charts"],
      cityOptions
    );

    this.roadInstance = this.$echarts.initCharts(
      this.roadInstance,
      this.$refs["road-charts"],
      roadOptions
    );

    this.vehicleInstance = this.$echarts.initCharts(
      this.vehicleInstance,
      this.$refs["vehicle-charts"],
      vehicleOptions
    );
  },
  methods: {
    /**
     * 定义浮层的容器，绑定在当前层级上
     */
    getCalendarContainer() {
      return triggerNode => triggerNode.parentNode;
    },

    onClick(e) {
      console.log(e);
    },
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log("Received values of form: ", values);
        }
      });
    }
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
